.background-parent {
    /* position: relative; */
    background-color: #342547;
    /* width: 100%; */
    /* height: 100vh; */
    /* overflow: hidden; */
    padding-top: 80px;

}

.custom-background {
    /* position: absolute; */
    /* top: 20%;
    right: 0; */
    width: 93%;
    height: 100%;
    margin: 0 auto;
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
    background: linear-gradient(180deg,
            rgba(118, 91, 147, 0.70) 0%,
            rgba(31, 15, 48, 0.00) 100%);
    display: flex;

}

.banner-image-box {
    width: 100%;
}

.discover-img {
    width: 100%;
    margin-top: -50px;
}

.discover-heading {
    font-family: 'Poppins';
    font-size: 26px;
    font-style: normal;
    font-weight: 700;
    line-height: 170%;
    color: #FFF;
    margin-top: 8%;
    margin-left: 5%;
    letter-spacing: 5px;


}

.discover-text {
    color: #FFF;
    font-family: 'Poppins';
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    margin-left: 5%;
    flex-wrap: wrap;
    line-height: 170%;
    letter-spacing: 1.68px;
    margin-top: -20px;
    width: 75%;
}

.discover-sub_text {
    color: #FFF;
    font-family: 'Poppins';
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    margin-left: 5%;
    flex-wrap: wrap;
    line-height: 170%;
    margin-top: 5%;
    margin-bottom: 5%;

}

.discover-download {
    color: #FFF;
    font-family: 'Poppins';
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    margin-left: 5%;
    flex-wrap: wrap;
    line-height: 170%;
    bottom: 5%;
    position: relative;
    margin-bottom: 5%;

}

.iosimage-div {
    display: flex;
    position: relative;
    bottom: 8%;
    left: 5%;
}

.googleimage {

    height: 40px;
    width: 120px;
    margin-right: 20px;

}

.iosimage {

    flex: 0 0 150px;
    height: 40px;
    width: 120px;


    margin-left: 0%;
}

/* 
@media(max-width: 450px) {
    .custom-background {
        flex-direction: column-reverse;
    }

    .iosimage-div {
        flex-wrap: wrap;

    }
} */

@media (max-width:375px) {
    .iosimage {
        position: relative;
        /* bottom:80px; */

    }

    .iosimage-div {
        flex-wrap: wrap;

    }

    .custom-background {
        flex-direction: column-reverse;
    }


}

@media (max-width:425px) {

    .discover-img {

        margin-top: 0px !important;
    }


}

@media (min-width:1900px) {
    .discover-download {
        bottom: 0%;
    }

    .iosimage-div {
        bottom: 0%;
    }
}